export class OrderData {
    constructor(events) {
        this.formErrors = {};
        this.events = events;
        this._order = {
            payment: '',
            address: '',
            phone: '',
            email: '',
        };
    }
    set payment(payment) {
        this._order.payment = payment;
    }
    get order() {
        return this._order;
    }
    get payment() {
        return this._order.payment;
    }
    setOrderField(field, value) {
        this._order[field] = value;
        this.validateOrder();
    }
    validateOrder() {
        const errors = {};
        if (!this._order.address) {
            errors.address = 'Необходимо указать адрес';
        }
        if (!this._order.payment) {
            errors.payment = 'Необходимо выбрать способ оплаты';
        }
        this.formErrors = errors;
        this.events.emit('orderForm:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
    setContactsField(field, value) {
        this._order[field] = value;
        this.validateContacts();
    }
    validateContacts() {
        const errors = {};
        if (!this._order.email) {
            errors.email = 'Необходимо указать email';
        }
        if (!this._order.phone) {
            errors.phone = 'Необходимо указать номер телефона';
        }
        this.formErrors = errors;
        this.events.emit('contactsForm:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
    clearForm() {
        this._order = {
            payment: '',
            address: '',
            phone: '',
            email: '',
        };
    }
}
